.tour-card {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.35);
  border-radius: 30px;
  display: flex;
  font-size: 21px;
  line-height: 26px;
  font-weight: 300;
  min-height: 500px;
}

.tour-card__image {
  display: block;
  border-radius: 30px 0 0 30px;
  width: 350px;
  min-height: 500px;
  height: 100%;
  object-fit: cover;
}

.tour-card__content {
  padding: 56px 50px 50px 50px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.tour-card__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.tour-card__heading {
  font-size: 36px;
  line-height: 38px;
  font-weight: 800;
  color: @basic-green;
  margin: 0;
  margin-bottom: 30px;
}

.tour-card__popularity {
  display: flex;
  justify-content: flex-end;
  padding-top: 10px;
}

.tour-card__star {
  display: block;
  width: 29px;
  height: 27px;
  stroke: @basic-yellow;
  fill: none;
  margin-right: 8px;

  &:last-child {
    margin-right: 0;
  }

  &--filled {
    fill: @basic-yellow;
  }
}

.tour-card__footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
  padding-right: 10px;
  font-size: 18px;
  line-height: 22px;
  font-weight: normal;
}

.tour-card__inner-heading {
  color: @basic-black;
  font-size: 21px;
  line-height: 25px;
  margin: 0;
  margin-bottom: 15px;
}

.tour-card__includes-list {
  margin: 0;
  padding: 0;
  padding-top: 7px;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  margin-right: 20px;
}

.tour-card__includes-item {
  width: 60px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 60px 60px;
  border-radius: 50%;
  background-color: @basic-green;
  display: flex;
  justify-content: center;
  align-items: center;
  color: @basic-white;
  font-size: 24px;
  line-height: 29px;
  font-weight: 700;
  margin-right: 20px;
  margin-bottom: 20px;

  &:last-child {
    margin-right: 0;
  }

  &--hotel {
    background-image: url("../img/icons/hotel.svg");
  }

  &--transport {
    background-image: url("../img/icons/transport.svg");
  }
}

.tour-card__cities-list {
  padding: 0;
  margin: 0;
  margin-right: 20px;
  list-style: none;
}

.tour-card__price-value {
  display: block;
  color: @basic-green;
  font-size: 48px;
  line-height: 58px;
  font-weight: 800;
}

@media (max-width: 1200px) {
  .tour-card {
    display: block;
  }

  .tour-card__image {
    width: 100%;
    height: 300px;
    min-height: 300px;
    border-radius: 30px 30px 0 0;
  }
}
